<template>
    <div 
        class="card"
        @click="turn2github" >
        <img :src="pic"  slot="demo-pic"/>
        <h3>{{title}}</h3>
        <div class="tags">
            <span 
                v-for="(item, index) in _tags"
                :class="item.color" 
                :key="index" >
                {{item.tag}}
            </span>
        </div>
    </div>
</template>

<script>
    export default {
        props: ["pic", 'title', 'tags', 'turn2'],
        data: ()=>({
            colors: ['red', 'blue', 'orange', 'green']
        }),
        computed: {
            _tags () {
                let {tags, colors} = this
                tags = tags.map(item=>{
                    let number = parseInt(Math.random()*colors.length)
                    let color = colors[number]
                    return {tag: item, color: color}
                })
                return tags
            }
        },
        methods: {
            turn2github () {
                window.open(this.turn2)
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .card
        width 350px
        background-color #ffffff
        margin-bottom 30px
        box-shadow 0 10px 9px rgba(0, 0, 0, .3)
        padding 15px
        overflow hidden
        &:hover
            cursor pointer
            img 
                transform: scale(1.1);
                transition: all .2s ease;
            h3
                color: #ee4558;
        img 
            width 100%
            height auto
            margin-bottom 24px;
        h3
            font-size 16px;
            font-weight bold;
            margin-bottom 12px;
        .tags
            font-size 14px;
            height 50px;
            display: flex;
            flex-flow: row wrap;
            align-content: space-around;
            align-items: center;
            span 
                margin-right 9px;
                display inline-block
                &::before
                    content "# "
                    color #333
                &.red
                    color: #ee4558;
                &.blue
                    color: #1d86e1;
                &.orange
                    color: orange;
                &.green
                    color: #4fcf8f;
                &.purple
                    color: purple;
</style>



