<template>
    <div :class="class_title" @click="open" >
        <Icon :pic="pic" :size="size" ></Icon>
        <span >{{title}}</span>
    </div>
</template>

<script>
    import Icon from '../../src/components/icon.vue'

    export default {
        components: {Icon},
        props: ['title', 'size', 'pic', 'turn2'],
        computed: {
            class_title () {
                let {size, turn2} = this
                let can_click = ''
                if (turn2) can_click = 'click'
                return `title ${size} ${can_click}` 
            }
        },
        methods: {
            open () {
                let {turn2} = this
                if (turn2) window.open(turn2)
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .title
        display flex
        align-items center
        span 
            user-select:none;
        &.click
            cursor pointer
            &:hover
                font-weight bold
        &.big
            margin-bottom 21px;
            span 
                margin-left 10px;
                font-size 21px;
                font-weight bold;
        &.small
            margin-bottom 12px;
            span 
                margin-left 18px;
                font-size 14px;
                font-weight light;
</style>



