<template>
    <div>
        <img :src="require('../assets/images/triangle.png')" alt="" class="triangle">
        <div class="square"></div>
        <div class="circle"></div>
    </div>
</template>

<script>
    export default {
        
    }
</script>

<style scoped>
/*三角形*/ 
.triangle{
    position: absolute;
    top: 0;
    left: 0;
    width: 1rem;
    max-width: 100px;
    animation: triangle 60s infinite;
    animation-direction: alternate;
    opacity: .3;
}
@keyframes triangle {
    0%{
        transform: translate(0, 0) rotate(0);
    }
    98%{
        transform: translate(93vw, 600px) rotate(359deg);
    }
    100%{
        transform: translate(93vw, 600px) rotate(300deg);
    }
}
/*正方形*/
.square{
    max-width: 90px;
    max-height: 90px;
    width: 1rem;
    height: 1rem;
    box-sizing: border-box;
    border: .15rem solid #a7bdf4;
    opacity: .5;
    position: absolute;
    top: 222px;
    left: 0;
    animation: square 30s infinite;
    animation-direction: alternate;
}
@keyframes square {
    0%{
        transform: translate(0, 0) rotate(0);
    }
    95%{
        transform: translate(44vw, -260px) rotate(180deg);
    }
    100%{
        transform: translate(44vw, -260px) rotate(300deg);
    }
}
/*圆形*/
.circle {
    max-width: 80px;
    max-height: 80px;
    width: .8rem;
    height: .8rem;
    border: .15rem solid #7ee3b9;
    border-radius: 50%;
    opacity: 0;
    position: absolute;
    top: -120px;
    right: 33vw;
    animation: circle 21s infinite;
}
@keyframes circle {
    0%{
        transform: translate(0, 680px);
        opacity: 0;
    }
    100%{
        transform: translate(0, 0);
        opacity: .5;
    }
}
</style>


