<template>
    <div id="app">
        <!-- 头部 -->
        <MHeader/>
        <!-- 教育背景, 前端技能, 编程学习 -->
        <Info>
            <m-title slot="education" title="教育背景"     size="big"   :pic="education" />
            <m-title slot="education" title="西南石油大学" size="small" :pic="sy"/>

            <m-title slot="web" title="前端技能"         size="big"   :pic="web" />
            <m-title slot="web" title="html + css + js" size="small" :pic="html" />
            <m-title slot="web" title="VUE"             size="small" :pic="vue" />
            <m-title slot="web" title="React"           size="small" :pic="react" />
            <m-title slot="web" title="小程序"           size="small" :pic="min" />

            <m-title slot="study" title="编程学习"  size="big"   :pic="code" />
            <m-title slot="study" title="webpack4" size="small" :pic="webpack" :turn2="src_webpack" />
            <m-title slot="study" title="nginx"    size="small" :pic="nginx"   :turn2="src_nginx" />
            <m-title slot="study" title="Nuxt"     size="small" :pic="nuxt"    :turn2="src_nuxt" />
            <m-title slot="study" title="Koa2"     size="small" :pic="koa"     :turn2="src_koa" />
            <m-title slot="study" title="更多"      size="small" :pic="more"   :turn2="src_more" />
        </Info>
        <!-- 个人项目 -->
        <Demo>
            <Card 
                v-for="(item, index) in demos" 
                :title='item.title'
                :pic="item.pic"
                :tags="item.tags"
                :turn2="item.link"
                :key="index" >
            </Card>
        </Demo>
        <!-- footer -->
        <m-footer/>
        <!-- 动画 -->
        <animation/>
    </div>
</template>

<script>
import MHeader from '@/components/header'
import Info from '@/components/info'
import Demo from '@/components/demo'
import Card from "@/components/card"
import MFooter from "@/components/footer"
import animation from "@/components/animation"
import MTitle from "@/components/title"
import data from '../static/js/blog_data.js'

export default {
    name: 'app',
    components: {MHeader, Info, Demo, Card, MFooter, animation, MTitle},
    data: ()=>({
        demos: data.demos,
        web: require('@/assets/images/computer.png'),                        // pic--> 前端技能
        html: require('@/assets/images/html.png'),                           // pic--> html + css + js
        vue: require('@/assets/images/vuejs.png'),                           // pic--> vue
        react: require('@/assets/images/react.png'),                         // pic--> react
        code: require('@/assets/images/book.png'),                           // pic--> 编程学习
        webpack: require('@/assets/images/Webpack.png'),                     // pic--> webpack4
        nuxt: require('@/assets/images/nuxt.png'),                           // pic--> Nuxt
        nginx: require('../static/image/ngx.png'),                           // pic--> nginx
        koa: require('@/assets/images/server.png'),                          // pic--> koa
        min: require('@/assets/images/小程序.png'),                          // pic--> koa
        more: require('@/assets/images/more.png'),                           // pic--> more
        education: require('@/assets/images/education.png'),                 // pic--> 教育背景
        sy: require('@/assets/images/xlsy.png'),                             // pic--> 西南石油
        src_webpack: 'https://gitee.com/lby1024/webpack4/wikis/pages',       // src--> webpack
        src_nginx: 'https://gitee.com/lby1024/nginx/wikis/pages',            // src--> nginx
        src_nuxt: 'https://gitee.com/lby1024/nuxt/wikis/pages',              // src--> nuxt
        src_koa: 'https://gitee.com/lby1024/koa/tree/master/',               // src--> koa
        src_more: 'https://gitee.com/lby1024',                               // src--> more
    }),
}

</script>



