<template>
    <div class="info">
        <div class="item"> <slot name="education"/> </div>
        <div class="item"> <slot name="web"/>       </div>
        <div class="item"> <slot name="study"/>     </div>
    </div>
</template>

<script>
    import MTitle from '@/components/title'
    export default {
        components: {MTitle},
    }
</script>

<style lang="stylus" scoped>
    .info
        min-height 160px;
        max-width 800px;
        margin 0 auto ;
        background-color #f7f7f7;
        padding 50px 0;
        
    @media (min-width: 800px) 
        .info
            display flex
            justify-content space-between

    // 小屏幕
    @media (max-width : 800px) 
        .info
            width 90%
            margin 0 auto    
            .item
                margin-bottom 30px
</style>



