<template>
    <div 
        :style="bind_style_icon()"
        @click="open"
        :class="classs" >
    </div>
</template>

<script>
    export default {
        props: {
            pic: {
                type: String,
                default: require('../../src/assets/images/github.png')
            },// 图标图片
            size: {
                type: String,
                default: 'small'
            },// 图标大小
            turn2: {
                type: String,
                default: null
            },// 图标链接 ---如果有链接---> 图标将会有css:hover效果
        },
        computed: {
            classs () {
                let {size, turn2} = this
                let can_click = ''
                if (turn2) can_click = 'click'
                let str = `icon ${size} ${can_click}`
                return str
            }, // 大图标有链接 ---> class="icon big click" : 大图标没连接 ---> class="icon big"
        },
        methods: {
            bind_style_icon (pic) {
                return {'background-image': `url(${this.pic})`}
            },
            open () {
                let {turn2} = this
                if (turn2) window.open(turn2)
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .icon
        display inline-block;
        background-size 80%
        background-position center
        background-repeat no-repeat
        &.click:hover
            opacity .6
            cursor pointer
        &.big
            width 28px;
            height 28px;
        &.small
            width 21px;
            height 21px;    
</style>



