<template>
    <div class="header">
        <div class="container">
            <h3>Fun & Enjoy</h3>
            <p>趣味でヒーローにやってものだ。</p>
            <p>秃った、でも强くなった。</p>
            <div class="icons">
                <Icon 
                    v-for="(item, index) in icons" 
                    :pic="item.pic"
                    size="big"
                    :turn2="item.turn2"
                    :key="index" >
                </Icon>
            </div>
        </div>
    </div>
</template>

<script>
    import  Icon from '@/components/icon'
    export default {
        components: {Icon},
        data: () => ({
            icons: {
                github: {
                    pic: require('@/assets/images/github.png'),
                    turn2: 'https://github.com/lby1024'
                },
                gitee: {
                    pic: require('../../src/assets/images/gitee.png'),
                    turn2: 'https://gitee.com/lby1024'
                },
                email: {
                    pic: require('../../src/assets/images/email.png'),
                    turn2: null
                },
                phone: {
                    pic: require('../../src/assets/images/phone.png'),
                    turn2: null
                }
            }
        })
    }
</script>

<style lang="stylus" scoped>
.header
    background-color #fff;
    .container
        padding .8rem 0;
        margin 0 auto ;
        max-width 800px;
        h3
            font-weight bolder;
            margin-bottom 18px
            font-size 30px
        p
            line-height 1.8em;
        .icons
            max-width 800px;
            margin 0 auto;
            margin-top .3rem;
            .icon
                margin-right 18px;
                &:nth-child(3)
                    background-color #7fffd4
                    border-radius 50%
    // 大屏幕
    @media (min-width: 800px) 
        .container
            h3
                font-size 39px;
            P
                font-size 16px;
                
    // 小屏幕
    @media (max-width : 800px) 
        .container
            width 90%;
            h3
                font-size 30px;
            P
                font-size 12px;
</style>



